html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
  margin: 0;
  scroll-behavior: smooth;
}

body {
  background-color: #000;
}

#main {
  background: rgba(68, 63, 114, 0.1);
  position: relative;
  z-index: 1;
}

.main-wrapper {
  margin: 0 auto;
  max-width: 380px;
  height: 100vh;
  position: relative;
  z-index: 2;
}
@media (min-width: 768px) {
  .main-wrapper {
    max-width: 768px;
  }
}
@media (min-width: 1040px) {
  .main-wrapper {
    max-width: 120rem;
  }
}

.main-content {
  height: 100%;
  display: flex;
  justify-content: center;
}

.bright {
  width: 70%;
  margin: 0 5%;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 5%;
}
@media (min-width: 768px) {
  .bright {
    gap: 2.5rem;
    max-width: 67rem;
  }
}

@media (min-width: 768px) {
  .bright_img {
    width: 5.5rem;
    height: 5.5rem;
  }
}

.input-cont {
  position: relative;
  display: block;
  width: 100%;
  z-index: 1;
}

#bright-progress {
  z-index: 4;
  -webkit-appearance: none;
  width: 100%;
  height: 1rem;
  outline: none;
  border-radius: 1rem;
  background: #3080e248;
  position: relative;
  background: transparent;
}
#bright-progress:hover {
  cursor: pointer;
}

#bright-progress::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 2.4rem;
  width: 2.4rem;
  border-radius: 50%;
  background: #FF8906;
  z-index: 4;
}
#bright-progress::-webkit-slider-thumb:hover {
  transition: 0.2s ease;
  box-shadow: 0 0 0 1rem #ff8b0649;
}

#backgroundBar {
  z-index: 2;
  width: 100%;
  height: 1rem;
  border-radius: 1rem;
  background: #3080e248;
  position: absolute;
  top: 0;
}

#progressBar {
  z-index: 3;
  width: 0;
  height: 1rem;
  background: #FF8906;
  border-radius: 1rem;
  position: absolute;
  top: 0;
}

.borde-click::-webkit-slider-thumb {
  box-shadow: 0 0 0 1rem #ff8b06a6 !important;
  transition: 0.2s ease;
}
@media (min-width: 768px) {
  .borde-click::-webkit-slider-thumb {
    box-shadow: 0 0 0 2rem #ff8b06a6 !important;
  }
}

@media (min-width: 768px) {
  #bright-progress, #progressBar, #backgroundBar {
    height: 2rem;
  }

  #bright-progress::-webkit-slider-thumb {
    height: 4.4rem;
    width: 4.4rem;
  }
  #bright-progress::-webkit-slider-thumb:hover {
    transition: 0.2s ease;
    box-shadow: 0 0 0 2rem #ff8b0649;
  }
}